.card {
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
    border-radius: 10px;
    text-align: center;
    width: 50%;
    padding: 50px;
    position: absolute;
    left: 30%;
    top: 30%;
    height: 200px;
    color:rgba(29, 21, 21, 0.603);
    }

  .colorSel
  {
    position: absolute;
    left: 10%;
    
  }

  .title
  {
  position: relative;
  left: 10%;
  color:rgb(236, 230, 142);
  font-size: 70px;
  }